/* The container */
.check-content {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 16px;
  user-select: none;
  margin-top: 22px;
  padding-top: 5px;
  z-index: 0; }

/* Hide the browser's default checkbox */
.check-content input {
  position: relative;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0; }

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee; }

.margin-0 {
  margin-top: 0px;
  margin-bottom: 0px; }

/* On mouse-over, add a grey background color */
.check-content:hover input ~ .checkmark {
  background-color: #ccc; }

/* When the checkbox is checked, add a blue background */
.check-content input:checked ~ .checkmark {
  background-color: #2bab0d; }

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none; }

/* Show the checkmark when checked */
.check-content input:checked ~ .checkmark:after {
  display: block; }

/* Style the checkmark/indicator */
.check-content .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg); }

/* The container */
.radio-content {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 16px;
  user-select: none;
  margin-top: 22px;
  padding-top: 5px; }

/* Hide the browser's default radio button */
.radio-content input {
  position: absolute;
  opacity: 0;
  cursor: pointer; }

/* Create a custom radio button */
.radiomark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%; }

/* On mouse-over, add a grey background color */
.radio-content:hover input ~ .radiomark {
  background-color: #ccc; }

/* When the radio button is checked, add a blue background */
.radio-content input:checked ~ .radiomark {
  background-color: #2bab0d; }

/* Create the indicator (the dot/circle - hidden when not checked) */
.radiomark:after {
  content: "";
  position: absolute;
  display: none; }

/* Show the indicator (dot/circle) when checked */
.radio-content input:checked ~ .radiomark:after {
  display: block; }

/* Style the indicator (dot/circle) */
.radio-content .radiomark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white; }

.toast {
  position: fixed;
  top: 10px;
  left: 60%;
  right: 0;
  height: 8%;
  margin: auto;
  max-width: 350px;
  padding: 20px;
  color: #fff;
  font-family: sans-serif;
  text-align: center;
  border: 1px solid #aaaaaa;
  transition: opacity 0.2s, visibility 0.2s;
  z-index: 6000; }

.confirm {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  margin: auto;
  padding: 10px;
  color: #fff;
  transition: opacity 0.2s, visibility 0.2s;
  z-index: 6000;
  background: rgba(0, 0, 0, 0.7);
  opacity: 0;
  animation-name: confirm-open;
  animation-duration: 0.4s;
  animation-fill-mode: forwards;
  align-items: center;
  justify-content: center; }

.confirm-window {
  width: 100%;
  height: 35%;
  max-width: 600px;
  background: white;
  opacity: 0;
  transform: scale(0.75);
  animation-name: confirm-content-open;
  animation-duration: 0.4s;
  animation-fill-mode: forwards;
  animation-delay: 0.2s;
  display: grid;
  grid-row: 1hr 1hr 1hr; }

.confirm-content {
  line-height: 1.8em;
  background: white;
  color: black; }

.confirm-buttons {
  background: white;
  display: flex;
  justify-content: flex-end; }

.confirm-titlebar,
.confirm-content,
.confirm-buttons {
  padding: 1.25em; }

.confirm-close {
  background: none;
  outline: none;
  border: none;
  transform: scale(2.5);
  color: white;
  transition: color 0.15s; }

.confirm-titlebar {
  background: #2bab0d;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.confirm-title {
  font-weight: bold;
  font-size: 1.1em; }

@keyframes confirm-open {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes confirm-content-open {
  to {
    opacity: 1;
    transform: scale(1); } }

.show-toast {
  visibility: visible;
  opacity: 1; }

.hide-toast {
  visibility: hidden;
  opacity: 0; }

